.App {
  display: grid;
  grid-template-columns: repeat(3,1fr);
}


/* media query */
@media screen and (max-width:600px){
  .App{
    margin-top: 5px;
    display: grid;
    grid-template-columns: repeat(1,1fr);
  }
}
@media screen and (min-width:601px) and (max-width:800px){
  .App{
    margin-top: 5px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
  }
}